body {
    display: flex;
    flex-direction: column;
    width: 100vw;
    background-color: $white;
    overflow-x: hidden;

    @include breakpointsUp(xl) {
        height: 100vh;
        overflow-y: hidden;
    }

}

main {
    width: 100vw;
    flex-grow: 1;
    flex-shrink: 1;
}

section#hero {
    display: grid;
    place-items: center;
    height: 100%;
    padding: 7.5rem 0;
}

header {
    display: flex;
    place-items: center;
    width: 100vw;
    padding: 2.5rem 0;
    flex-grow: 0;
    flex-shrink: 1;
}

.logo {
    width: 10rem; height: auto;
}

h1 {
    font-size: 1.5rem;
    line-height: 1;
    font-weight: 500;
    color: $gray-900;
}

.hero__ribbon {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding: 0.5rem 0;
    background-color: $gold-500;
    color: $white;
    text-align: center;
}

.hero__grid {
    display: grid;
    grid-template-rows: repeat(2, minmax(0, 1fr));
    row-gap: 1.875rem;

    @include breakpointsUp(xl) {
        grid-template-rows: none;
        grid-template-columns: 1fr 23.125rem;
        column-gap: 1.875rem;
    }

}

.video {
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    border: 0;
}

.chat {
    width: 100%; height: 100%;
}

.video-wrapper {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%;
    box-shadow: 0px 25px 50px -12px rgba(0,0,0,0.25);
}

.hero__right {
    border: 1px solid $gray-100;
    background-color: $gray-100;
}

footer {
    display: grid;
    place-items: center;
    width: 100vw;
    padding: 1.25rem 0;
    flex-grow: 0;
    flex-shrink: 1;

    @include breakpointsUp(medium) {
        padding: 2.5rem 0;
    }

}